<template>
  <div>
	<section id="content1"></section>
  </div>
</template>

<script>
export default {
  name: 'Swipetest',
  mounted: function() { 
      var startY = null;
      var startX = null;
      var endY = null;
      var endX = null;
      window.addEventListener('load', function(){
        // „Çπ„ÉØ„Ç§„ÉóÔºè„Éï„É™„ÉÉ„ÇØ
        document.getElementById("content1").addEventListener('touchmove', logSwipe);
        // „Çø„ÉÉ„ÉÅÈñãÂßã
        document.getElementById("content1").addEventListener('touchstart', logSwipeStart);
        // „Çø„ÉÉ„ÉÅÁµÇ‰∫Ü
        document.getElementById("content1").addEventListener('touchend', logSwipeEnd);
      });
      function logSwipeStart(event) {
          event.preventDefault();
          startY = event.touches[0].pageY;
          startX = event.touches[0].pageX;
      }
      function logSwipe(event) {
        event.preventDefault();
        endY = event.touches[0].pageY;
		endX = event.touches[0].pageX;
      }
      function logSwipeEnd(event) {
        event.preventDefault();
        if( 0 < (endY - startY) ) {
          console.log("‰∏ãÂêë„Åç");
        } else {
          console.log("‰∏äÂêë„Åç");
        }
        if( 0 < (endX - startX) ) {
          console.log("Âè≥Âêë„Åç");
        } else {
          console.log("Â∑¶Âêë„Åç");
        }
      }
  }
}
</script>

